/* Forms
/* ---------------------------------------------------------- */

.gh-input,
.gh-textarea {
	background: var(--color-bg);
	border-radius: var(--radius);
	border: none;
	border: solid 1px var(--color-border);
	color: inherit;
	display: block;
	outline: 0;
	padding: 0 0.6em;
	text-decoration: none;
	width: 100%;
}

.gh-input:focus {
	border-color: var(--color-primary);
}

.gh-select {
	height: var(--height);
	padding-right: var(--height);
	text-overflow: ellipsis;
}

.gh-select option {
	color: var(--color-primary);
	background: var(--color-bg);
}

.gh-select:focus::-ms-value {
	background-color: transparent;
}

.gh-select::-ms-expand {
	display: none;
}

.gh-input,
.gh-select {
	height: var(--height);
}

.gh-textarea {
	padding: 0.3em 0.6em;
	resize: vertical;
}

.gh-check {
	display: block;
	margin-right: -2em;
	opacity: 0;
	width: 1em;
	z-index: -1;
}

.gh-check + label,
.gh-check + label {
	display: flex;
	align-items: center;
	color: var(--color-base);
	cursor: pointer;
	font-size: 1em;
	font-weight: var(--font-normal);
	padding-left: calc((var(--height) * 0.6) + 0.75em);
	padding-right: 2rem;
	position: relative;
	user-select: none;
}

.gh-check + label:before,
.gh-check + label:before {
	background: var(--color-bg);
	border-radius: var(--radius);
	border: solid 1px var(--color-border);
	content: '';
	display: inline-block;
	height: calc(var(--height) * 0.6);
	line-height: calc(var(--height) * 0.56);
	text-align: center;
	width: calc(var(--height) * 0.6);
	margin-right: 1rem;
}

.gh-check:checked + label:before,
.gh-check:checked + label:before {
	background: var(--color-primary);
	border-color: var(--color-primary);
	color: var(--color-bg);
	content: '✓';
}

.gh-check:focus + label:before,
.gh-check:focus + label:before {
	border-color: var(--color-primary);
	box-shadow: 0 0 0 1px var(--color-primary);
}


.gh-check + label:before {
	border-radius: var(--radius);
}

.gh-check + label:before {
	border-radius: 100%;
}
